import Vue from "vue";
import VueRouter from "vue-router";
import Task from "@/views/Task.vue";
import Map from "@/views/Map.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "task",
    component: Task,
  },
  {
    path: "/map",
    name: "map",
    component: Map,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
